export default {
  setIsShowFloatWindow: function (state, payload) {
    state.isShowFloatWindow = payload.data;
  },
  setPlayingSectionId: function (state, payload) {
    state.playingSectionId = payload.data;
  },
  setPlayingAlbumData: function (state, payload) {
    state.playingAlbumData = payload.data;
  },
  setPlayingAlbumId: function(state, payload) {
    state.playingAlbumId = payload.data;
  },
  setPlayingSectionData: function(state, payload) {
    state.playingSectionData = payload.data;
  }
}


