import Vue from 'vue'
import Router from 'vue-router'

// ? 页面跳转时顶部的进度条
import NProgress from 'nprogress';
import 'nprogress/nprogress.css';

import { routes } from '../router/route/index'
import { db } from '../utils/lowdb';
import { apiLogin } from '../service/api/login';
import { apiUserDetail } from '../service/api/user';
import { Toast } from 'vant';
import { wechatLogin } from '../service/wechatAuth';

// ? 进度条配置
NProgress.configure({
  showSpinner: false
});

Vue.use(Router)

export const router = new Router({
  mode: 'history',
  routes,
})

router.beforeEach(async (to, from, next) => {
  NProgress.start();

  // ? 路由切换时，带着来源页面的名称
  to.meta.fromRouteName = from.name || null;
  
  /*
    todo 验证是否登录逻辑：
    0. 不验证登录的页面 : blank
    1. 本地没有token
    2. url 上有 code 并且需要登录，需要登录的参数是调用 wechatLogin() 里面写上的，为了防止 url 中有 code 但是并不需要登录的情况
    3. 本地没有用户信息
    4. token 有，且用户信息正常
  */
  if (to.name === 'blank') {
    next();
  } else if ( notToken(to.query) ) {
    wechatLogin();
  } else if ( needLogin(to.query) ) {
    let code = (Array.isArray( to.query.code ) && to.query.code.length > 0)
      ? to.query.code[to.query.code.length - 1]
      : to.query.code;

    const loginAuthResponse = await apiLogin(code);
    if (!loginAuthResponse || loginAuthResponse.type === 'fail') return Toast('意外的登录错误，请联系客服');
    console.log( '拿 code 请求接口的返回数据: ', loginAuthResponse );
    db.set('token', loginAuthResponse.data.token).write();
    db.unset( 'needLoginFlag' ).write();
    
    const userDetailResponse = await apiUserDetail();
    if (!userDetailResponse || userDetailResponse.type === 'fail') return Toast('意外的用户登录错误，请联系客服');
    console.log('获取用户信息的接口返回数据: ', userDetailResponse);
    db.set('userInfo', userDetailResponse.data).write();

    next();
  } else if ( notUserInfo() ) {
    const userDetailResponse = await apiUserDetail();
    if (!userDetailResponse || userDetailResponse.type === 'fail') return Toast('意外的用户登录错误，请联系客服');
    console.log('获取用户信息的接口返回数据: ', userDetailResponse);
    db.set('userInfo', userDetailResponse.data).write();

    next();
  } else {
    next();
  }
});

router.afterEach((to, from) => {
  NProgress.done();
});



// todo 封装 Router.beforeEach 中使用的验证登录逻辑
function notToken(query) {
  let token = db.get('token').value(),
      needLoginFlag = db.get('needLoginFlag').value();
  return !token && !(query.code && query.state && needLoginFlag)
}
function needLogin(query) {
  let needLoginFlag = db.get('needLoginFlag').value();
  return query.code && query.state && needLoginFlag
}
function notUserInfo() {
  let userInfo = db.get('userInfo').value();
  return !userInfo;
}


