const { tabRoutes } = require('./tab');
const { myChildRoutes } = require('./myChild');
const { directBuyRoutes } = require('./directBuy');

module.exports = {
  routes: [
    ...tabRoutes,
    ...myChildRoutes,
    ...directBuyRoutes,
    {
      path: '/',
      redirect: '/morning-parents'
    },
    {
      path: '/play-music',
      name: 'play-music',
      component: resolve => require(['@/pages/playMusic'], resolve)
    },
    {
      path: '/choose-identity',
      name: 'choose-identity',
      component: resolve => require(['@/pages/chooseIdentity'], resolve)
    },
    {
      path: '/activate-verify-code',
      name: 'activate-verify-code',
      component: resolve => require(['@/pages/activateVerifyCode'], resolve)
    },
    {
      path: '/bind-phone',
      name: 'bind-phone',
      component: resolve => require(['@/pages/bindPhone'], resolve)
    },
    {
      path: '/album-introduction',
      name: 'album-introduction',
      component: resolve => require(['@/pages/albumIntroduction'], resolve)
    },
    {
      path: '/album-catalogue',
      name: 'album-catalogue',
      component: resolve => require(['@/pages/albumCatalogue'], resolve)
    },
    {
      path: '/comment-detail',
      name: 'comment-detail',
      component: resolve => require(['@/pages/commentDetail'], resolve)
    },
    {
      path: '/total-message',
      name: 'total-message',
      component: resolve => require(['@/pages/totalMessage'], resolve)
    },
    {
      path: '/send-message',
      name: 'send-message',
      component: resolve => require(['@/pages/sendMessage'], resolve)
    },
    {
      path: '/send-comment',
      name: 'send-comment',
      component: resolve => require(['@/pages/sendComment'], resolve)
    },
    {
      path: '/reply-message',
      name: 'reply-message',
      component: resolve => require(['@/pages/replyMessage'], resolve)
    },
    {
      path: '/total-album',
      name: 'total-album',
      component: resolve => require(['@/pages/totalAlbum'], resolve)
    },
    {
      path: '/detail-message',
      name: 'detail-message',
      component: resolve => require(['@/pages/detailMessage'], resolve)
    },
    {
      path: '/blank',
      name: 'blank',
      component: resolve => require(['@/pages/blank'], resolve)
    }
  ]
}


