export default {
  namespaced: true,

  state: {
    audio: null,
    playStatus: false,
    duration: 0,
    currentTime: 0,
    playbackRate: 1,
  },

  getters: {
    audio: function(state) {
      return state.audio;
    },
    playStatus: function(state) {
      return state.playStatus;
    },
    duration: function(state) {
      return state.duration;
    },
    currentTime: function(state) {
      return state.currentTime;
    },
    playbackRate: function(state) {
      if (state.audio === null) return ;
      return state.audio.playbackRate;
    }
  },

  mutations: {
    setPlayStatus: function (state, payload) {
      state.playStatus = payload.data;
    },
    initAudio: function (state, payload) {
      state.audio = payload.data;
    },
    setDuration: function (state, payload) {
      state.duration = payload.data;
    },
    setCurrentTime: function (state, payload) {
      state.currentTime = payload.data;
    },
    setAudioSrc: function (state, payload) {
      if (state.audio === null) return ;
      state.audio.src = payload.data;
    },
    setAudioPlaybackRate: function (state, payload) {
      if (state.audio === null) return ;
      state.audio.playbackRate = payload.data;
      state.playbackRate = payload.data;
    }
  },

  actions: {
    
  }
}


