import { db } from "../utils/lowdb";

export function wechatLogin() {
  db.set('needLoginFlag', true).write(); // 添加需要登录的标记
  let redirect_uri = encodeURIComponent( window.location.href );
  let url = `https://open.weixin.qq.com/connect/oauth2/authorize?appid=wx6dd85d5fe5222d19&redirect_uri=${redirect_uri}&response_type=code&scope=snsapi_userinfo&state=STATE#wechat_redirect`;
  window.location.href = url;
}


