import axios from './axios';
import { db } from '../utils/lowdb';
import { wechatLogin } from './wechatAuth';

// ? const { method = 'GET', url, auth = true, data = {} } = config;
export const request = (config) => {
  checkConfigIsComplete(config);
  // if (/h5\/order/.test( config.url )) {
  //   axios.defaults.baseURL = 'https://api2.qdspub.cn';
  // }
  return new Promise((resolve, reject) => {
    const { method = 'GET', url, auth = true, data = {} } = config;
    const headers = {};
    if (auth === true) {
      let token = db.get('token').value();
      headers['Authorization'] = `Bearer ${token}`;
    }
    
    axios({
      headers,
      method,
      url,
      data,
    }).then(response => {
      if (response.status === 200) {
        if (response.data.code === '98') return wechatLogin();
        resolve(response.data);
      } else {
        console.log( 'code不是200的返回内容: ', response );
      }
    }).catch(error => {
      reject( 'axios 请求失败: ', error.response );
    })
  }).catch(error => {
    console.log( 'https? 请求失败: ', error.response );
    wechatLogin(); // ! 这是伪处理，实际上没有收到 401 状态码
  })
}

function checkConfigIsComplete(config) {
  const { url } = config;
  if (!url) {
    throw new Error( "Request must have a url, please check this param!" );
  }
}

