import store from '../store/index';

export function onGlobalAudioTimeUpdate(e) {
  const currentTime = parseInt(store.getters['audioState/audio'].currentTime);
  store.commit( 'audioState/setCurrentTime', { data: currentTime } );
}

export function onGlobalAudioEnded(e) {
  store.commit( 'audioState/setPlayStatus', { data: false } );
}

export function onGlobalAudioCanplay(e) {
  const duration = parseInt(store.getters['audioState/audio'].duration);
  store.commit( 'audioState/setDuration', { data: duration } );
}


