import { request } from '../request';

export function apiLogin(loginCode) {
  return request({
    method: 'GET',
    url: `/auth/h5/manualauth/${loginCode}`,
    auth: false,
    data: {}
  }).then(result => {
    if (result.code === '0') {
      return Promise.resolve({
        type: 'success',
        data: result.data
      })
    } else {
      const message = getErrorMsg(result.code);
      return Promise.resolve({
        type: 'fail',
        message: result.message || message
      })
    }
  }).catch(err => {
    console.log( '接口错误: ', err );
  })
}

export const API_LOGIN_ERRORS = {
  '001': '用户信息异常',
  '003': '用户信息异常',
  '004': '用户信息异常',
  '010': '用户信息异常',
  '100': '未知错误'
}


