// flt 秒转换分钟00:00:00格式
export function fltTimeToMinute(value){
  var secondTime = parseInt(value);
  var minuteTime = 0;
  var hourTime = 0;
  if(secondTime > 60) {
    minuteTime = parseInt(secondTime / 60);
    secondTime = parseInt(secondTime % 60);
    if(minuteTime > 60) {
      hourTime = parseInt(minuteTime / 60);
      minuteTime = parseInt(minuteTime % 60);
    }
  }
  var result = parseInt(secondTime) < 10 ? '0' + parseInt(secondTime) : "" + parseInt(secondTime);
  if(minuteTime > 0) {
    result = parseInt(minuteTime) < 10 ? "0" + parseInt(minuteTime) + ":" + result : "" + parseInt(minuteTime) + ":" + result;
  }
  if(minuteTime === 0) {
    result = '00:' + result;
  }
  if(hourTime > 0) {
    if(hourTime > 0) {
      result = parseInt(hourTime) < 10 ? "0" + parseInt(hourTime) + ":" + result : "" + parseInt(hourTime) + ":" + result;
    }
    if(hourTime === 0) {
      result = parseInt(hourTime) < 10 ? "0" + parseInt(hourTime) + ":" + result : "" + parseInt(hourTime) + ":" + result;
    }
  }
  return result;
}

// flt 转换为多长时间前 5天前
export function fltTimeInterval(oldTime) {
  if (oldTime === null) return '';
  
  oldTime = new Date( oldTime.replace(/-/g, '/') );
  if (oldTime.toString() === 'Invalid Date') return '';
  
  let now = new Date();
  let [ y, m, d ] = [
    now.getFullYear(),
    now.getMonth() + 1,
    now.getDate(),
  ];

  let timeSpace = new Date( `${y}-${m}-${d} 00:00:00` ).getTime() - oldTime.getTime();

  let yearStamp = 365 * 24 * 60 * 60 * 1000,
      monthStamp = 30 * 24 * 60 * 60 * 1000,
      weekStamp = 7 * 24 * 60 * 60 * 1000,
      dayStamp = 24 * 60 * 60 * 1000;

  let year = Math.floor(timeSpace / yearStamp);
  if ( year > 0 ) return year + '年前';

  let month = Math.floor(timeSpace / monthStamp);
  if ( month > 0 ) return month + '个月前';

  let week = Math.floor(timeSpace / weekStamp);
  if (week > 0) return week + '周前';

  let day = Math.floor(timeSpace / dayStamp);
  if (day > 0) return day + '天前';

  return '今天';
}


