<template>
  <div ref="componentbox" class="float-window-container f-row-between">
    
    <div
      class="cover"
      :style="{ 'background-image': `url( ${playingAlbumData.cover} )` }"
    />
    <div class="content f-row-between">
      <div class="row f-col-between">
        <div class="first-row one-ellipsis">
          {{ playingSectionData.title }}
        </div>
        <div class="two-row one-ellipsis">
          {{ playingSectionData.duration | fltTimeToMinute }}
          <span>{{ playingAlbumData.name }}</span>
        </div>
      </div>
      <div class="oper-button f-row-between-c">
        <div @click="switchPlayStatus" class="control f-i-c">
          <img v-if="playStatus === true" src="@/assets/images/float_window_pause.png" alt="">
          <img v-else src="@/assets/images/float_window_play.png" alt="">
        </div>
        <img @click="closeFloatWindow" class="close" src="@/assets/images/float_window_close.png" alt="">
      </div>
    </div>

    <div
      class="progress"
      :style="{ 'width': `${currentTime / duration * progressLimit}px` }"
    />

  </div>
</template>

<script>
import { mapState } from 'vuex';

export default {
  data() {
    return {
      progressLimit: 0,
    }
  },

  computed: mapState({
    isShowFloatWindow: state => state.isShowFloatWindow,
    playingAlbumData: state => state.playingAlbumData,
    playingAlbumId: state => state.playingAlbumId,
    playingSectionId: state => state.playingSectionId,
    playingSectionData: state => state.playingSectionData,
    playStatus: state => state.audioState.playStatus,
    audio: state => state.audioState.audio,
    playbackRate: state => state.audioState.playbackRate,
    currentTime: state => state.audioState.currentTime,
    duration: state => state.audioState.duration,
  }),

  mounted() {
    this.progressLimit = this.$refs.componentbox.clientWidth;
  },

  methods: {
    /*
      todo 1. 当前为播放中，调用暂停方法
      todo 2. 当前为暂停中，调用播放方法
    */
    switchPlayStatus() {
      if (!this.audio.src) return ;
      if (!this.playStatus) {
        this.switchToPlaying();
      } else {
        this.switchToPaused();
      }
    },

    // todo 切换播放状态，修改状态管理中的音频播放状态，设置此时用户设置的倍速
    switchToPlaying() {
      this.audio.playbackRate = this.playbackRate;
      this.audio.play();
      this.$store.commit( 'setIsShowFloatWindow', { data: true } );
      this.$store.commit( 'audioState/setPlayStatus', { data: true } );
    },
    switchToPaused() {
      this.audio.pause();
      this.$store.commit( 'audioState/setPlayStatus', { data: false } );
    },

    closeFloatWindow() {
      this.$store.commit( 'audioState/setPlayStatus', { data: false } );
      this.$store.commit( 'audioState/setAudioSrc', { data: '' } );
      this.$store.commit( 'setIsShowFloatWindow', { data: false } );
    }
  }
}
</script>

<style lang="scss" scoped>
.float-window-container {
  height: 62px;
  width: 100%;
  position: relative;
  border-radius: 3px;

  .cover {
    width: 45px;
    height: 62px;
    border-radius: 3px 0 0 3px;
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
  }
  .content {
    box-sizing: border-box;
    width: calc(100% - 45px);
    padding: 10.5px 10px;

    .row {
      width: calc(100% - 60px - 25px);
      
      .first-row {
        width: 100%;
        font-size: 14px;
        font-weight: 600;
        color: rgba(51,51,51,1);
      }
      .two-row {
        font-size: 11px;
        color: rgba(153,153,153,1);

        span {
          margin-left: 10px;
        }
      }
    }
    .oper-button {
      width: 60px;
      
      .control {
        img {
          width: 30px;
          height: 30px;
        }
      }
      .close {
        width: 15px;
        height: 15px;
      }
    }
  }

  .progress {
    height: 2px;
    background: rgba(244,186,65,1);
    border-radius: 3px;
    position: absolute;
    bottom: 0;
    left: 0;
  }
}
</style>


