module.exports = {
  myBabyRoutes: [
    {
      path: '/my/my-baby/list',
      name: 'baby-list',
      component: resolve => require(['@/pages/my/myBaby/list'], resolve)
    },
    {
      path: '/my/my-baby/add',
      name: 'baby-add',
      component: resolve => require(['@/pages/my/myBaby/add'], resolve)
    },
    {
      path: '/my/my-baby/edit',
      name: 'baby-edit',
      component: resolve => require(['@/pages/my/myBaby/edit'], resolve)
    },
  ]
}


