const errorMap = {
  '001': '请求参数不合法',
  '003': '数据重复',
  '004': '数据对象不存在',
  '010': '业务操作失败',
  '98': '鉴权失败',
  '100': '未知错误'
}

export function getErrorMsg(errCode) {
  let message = errorMap[ errCode ];
  return message || `未约定的 errCode ${errCode}`;
}


