const { myBabyRoutes } = require('./myBaby');

module.exports = {
  myChildRoutes: [
    ...myBabyRoutes,
    {
      path: '/my/my-bought',
      name: 'my-bought',
      component: resolve => require(['@/pages/my/myBought'], resolve)
    },
    {
      path: '/my/my-listen-history',
      name: 'my-listen-history',
      component: resolve => require(['@/pages/my/myListenHistory'], resolve)
    },
    {
      path: '/my/my-publish',
      name: 'my-publish',
      component: resolve => require(['@/pages/my/myPublish'], resolve)
    },
    {
      path: '/my/mechanism-info',
      name: 'mechanism-info',
      component: resolve => require(['@/pages/my/mechanismInfo'], resolve)
    }
  ]
}


