<template>
  <div id="app">
    <router-view/>
    
    <div
      v-if="passFloatWindowConditions()"
      class="float-window-box"
    >
      <FloatWindow />
    </div>

    <!-- 全局弹框 -->
    <CommonPopup
      v-if="isShowGlobalPopup"
    >
      <!-- 要填入弹框中的内容 -->
      <template v-slot:popup-content>
        <BindPhone @click="clickBindPhone" v-if="!userIsBindPhone" />
      </template>
    </CommonPopup>

  </div>
</template>

<script>
import VConsole from 'vconsole';
import { onGlobalAudioTimeUpdate, onGlobalAudioEnded, onGlobalAudioCanplay } from './utils/audio';
import FloatWindow from './components/floatWindow/FloatWindow.vue';
import CommonPopup from '@/components/globalPopup/CommonPopup.vue';
import { db } from './utils/lowdb';

// import VipGiftBox from '@/components/globalPopup/VipGiftBox.vue';
import BindPhone from '@/components/globalPopup/BindPhone.vue';
// import NewUserPresent from '@/components/globalPopup/NewUserPresent.vue';
// import JoinGroup from '@/components/globalPopup/JoinGroup.vue';

export default {
  name: 'App',
  
  components: {
    FloatWindow,
    CommonPopup,
    BindPhone
  },

  data() {
    return {
      noShowPages: [
        'activate-verify-code',
        'bind-phone',
        'choose-identity',
        'baby-list',
        'baby-edit',
        'baby-add',
        'direct-buy',
        'vip-card',
        'play-music',
        'album-catalogue'
      ],

      isShowGlobalPopup: false,

      userIsBindPhone: true
    }
  },

  created() {
    var vc = new VConsole();
    this.initGlobalAudio();
    this.checkUserIsBindPhone();
  },

  methods: {
    // todo 跳转至绑定手机号的页面
    clickBindPhone() {
      this.isShowGlobalPopup = false;
      this.$router.push({ name: 'bind-phone' });
    },

    // todo 检查当前用户是否绑定了手机号
    checkUserIsBindPhone() {
      let userInfo = db.get('userInfo').value();
      if (!userInfo.mobileNo) {
        this.isShowGlobalPopup = true;
        this.userIsBindPhone = false;
      }
    },

    // todo 判断浮窗的显示条件
    passFloatWindowConditions() {
      return this.$store.getters.isShowFloatWindow &&
      !this.noShowPages.includes( this.$route.name );
    },

    initGlobalAudio() {
      if (this.$store.getters['audioState/audio'] !== null) return ;

      const audio = document.createElement( 'audio' );
      this.$store.commit( 'audioState/initAudio', { data: audio } );

      audio.addEventListener('timeupdate', onGlobalAudioTimeUpdate);
      audio.addEventListener('ended', onGlobalAudioEnded);
      audio.addEventListener('canplay', onGlobalAudioCanplay);
    },
  }
}
</script>

<style lang="scss" scoped>
#app {
  width: 100%;
  height: 100%;

  .float-window-box {
    width: calc(100% - 32px);
    margin: 0 16px;
    box-sizing: border-box;
    background: rgba(255,255,255,1);
    box-shadow: 0px 2px 5px 0px rgba(0, 0, 0, 0.2);
    position: fixed;
    bottom: 70px;
  }
}
</style>


