// ? 专辑类型
const ALBUM_TYPE = {
  '0': '专栏',
  '1': '听书',
  '2': '课程',
  '3': '有声书',
  '4': '小课',
  '5': '故事'
}

// ? 栏目模板类型
const TEMPLATES = {
  '1': 'BaseNewsLayout',
  '2': 'BaseListLayout',
  '3': 'BaseModularLayout'
}

export function getAlbumType(type) {
  return ALBUM_TYPE[ type ] || '默认';
}

export function getTemplateName(templateId = '1') {
  return TEMPLATES[ templateId ];
}


