<template>
  <div class="common-container">

    <van-overlay :show="isShowOverlay" class="f-c">
      <div class="content">
        <slot name="popup-content"></slot>
        <img @click="isShowOverlay = false" class="close" src="@/assets/images/global_popup_close.png" alt="">
      </div>
    </van-overlay>

  </div>
</template>

<script>
import Vue from 'vue';
import { Overlay } from 'vant';

Vue.use(Overlay);

export default {
  data() {
    return {
      isShowOverlay: true
    }
  }
}
</script>

<style lang="scss" scoped>
.common-container {
  width: 100%;
  height: 100%;

  .content {
    width: 240px;
    max-height: 80%;
    margin: auto;
    position: relative;
    border-radius: 10px;

    .close {
      width: 27px;
      height: 27px;
      position: absolute;
      top: -30px;
      right: -20px;
    }
  }
}
</style>


