module.exports = {
  directBuyRoutes: [
    {
      path: '/direct-buy/:albumid',
      name: 'direct-buy',
      component: resolve => require(['@/pages/directBuy'], resolve)
    },
    {
      path: '/vip-card/:id?',
      name: 'vip-card',
      component: resolve => require(['@/pages/vipCard'], resolve)
    }
  ]
}


