import { request } from '../request';
import { getErrorMsg } from '../interfaceRules/errorMsg';

/**
 * todo 绑定手机号
 * @param { string } phoneNumber 手机号
 */
export function apiBindPhone(phoneNumber) {
  return request({
    method: 'POST',
    url: '/user/bind-phonenumber',
    data: {
      phoneNumber
    }
  }).then(result => {
    if (result.code === '0') {
      return Promise.resolve({
        type: 'success',
        data: result.data
      })
    } else {
      const message = getErrorMsg(result.code);
      return Promise.resolve({
        type: 'fail',
        message: result.message || message
      })
    }
  }).catch(err => {
    console.log( '接口错误: ', err );
  })
}

/**
 * todo 获取用户详情
 */
export function apiUserDetail() {
  return request({
    method: 'GET',
    url: '/user/detail'
  }).then(result => {
    if (result.code === '0') {
      return Promise.resolve({
        type: 'success',
        data: result.data
      })
    } else {
      const message = getErrorMsg(result.code);
      return Promise.resolve({
        type: 'fail',
        message: result.message || message
      })
    }
  }).catch(err => {
    console.log( '接口错误: ', err );
  })
}


